
.top {
  width: 1000px;
  margin: 0 auto;
  position: relative;
}
.top > .head {
  position: absolute;
  top: 0;
  right: 0;
}
.top > .head > i {
  display: inline-block;
  width: 50px;
  height: 50px;
  background: url("../assets/touxiang.png");
  background-size: cover;
}
.top > .head > span {
}
.title {
  font-size: 32px;
  font-weight: 700;
  line-height: 130px;
}
.desc {
  color: #313131;
  font-size: 20px;
  line-height: 30px;
  clear: both;
  margin-top: 72px;
}
.time {
  font-size: 15px;
  color: #969899;
  line-height: 30px;
  float: right;
}
.category {
  float: left;
}
.category > ul {
  list-style: none;
  overflow: hidden;
}
.category > ul > li {
  width: auto;
  padding: 0 20px;
  text-align: center;
  height: 30px;
  line-height: 30px;
  border-radius: 25px;
  float: left;
  border: 1px solid #dcdee0;;
  margin-right: 25px;
  color: #969899;
}
.work {
  width: 100%;
  background: #f8f9fa;
  overflow: hidden;
  margin-top: 120px;
  padding: 150px 0;
}
.worklist {
  width: 1000px;
  margin: 0 auto;
}

